.clear {
  &:after {
    content: '';
    overflow: hidden;
    clear: both;
    display: block;
  }
}


.Overlay {
  position: fixed;
  width: 100%;
  height: 100vh;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -51;
  //display: none;
  background: rgba(14, 14, 14, 0.4196078431372549);
  opacity: 0;
  visibility: hidden;

  &.ShowIt {
    display: block;
  }
}


%overlay {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  content: '';
}

%after {
  content: '';
  display: block;
  overflow: hidden;
  clear: both;
}

%bg {
  background-size: cover !important;
  background-position: center !important;
  background-repeat: no-repeat !important;
  background-color: #f1f1f1;
  //box-shadow: inset 0 0 80px rgba(0,0,0,0.5);
}

%img-position {
  position: absolute;
  top: 0;
  width: 100%;
  left: 0;
  height: 100%;
}

%parallax {
  background-repeat: no-repeat !important;
  background-attachment: fixed !important;
  background-position: center !important;
  background-size: cover !important;
}

%padding {
  content: '';
  display: block;
}


%link {
  position: absolute;
  height: 100%;
  width: 100%;
  display: block;
  z-index: 2;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

%position {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}


.banner-loader {
  position: relative;

  .container {
    position: relative;
    z-index: 5;
  }

  &:before {
    content: '';
    position: absolute;
    background: url('../images/static/blur-background.jpg');
    //background: rgba(83, 135, 200, 0.74117647058);
    z-index: 2;
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    transition: opacity .4s ease;
  }

  &.Loaded {
    &:before {
      opacity: 0;
    }
  }
}

.Loader {
  @extend %bg;
  position: relative;

  &:after {
    content: '';
    height: 100%;
    width: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    //background: #000000b5;
    position: absolute;
    background-image: url('../images/static/blur-background.jpg');
    @extend %bg;
    transition: .5s ease;
  }

  &.loaded:after {
    opacity: 0;
  }
}


.modify-img {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;

  &.no-pos {
    position: relative;
    left: auto;
    right: auto;
    top: auto;
    bottom: auto;
    height: 100%;
    width: 100%;
  }

}


//---------------- preloader
.pace {
  -webkit-pointer-events: none;
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}


.pace-inactive .pace-progress {
  display: none;
}

.pace .pace-progress {
  position: fixed;
  top: 0;
  right: 100%;
  width: 100%;
  height: 2px;
  z-index: 9999;
  background: $hover;
}

.pace-done .custom-loader img {
  display: none
}

//------------------- preloader end


.map-popup {
  width: 320px;

  &:after {
    @extend %after;
  }

  img {
    float: left;
    width: 120px;
  }

  .texts {
    float: right;
    width: calc(100% - 131px);
  }
}

