.textUp {
  span {
    opacity: 0;
    transform: translateY(15px);
    display: inline-block;
    transition: all .5s ease;
    visibility: hidden;
  }

  @for $i from 1 through 30 {
    span:nth-child(#{$i}) {
      transition-delay: .04s * $i;
    }
  }

  &.anim-active {
    span {
      opacity: 1;
      transform: none;
      visibility: visible;
    }
  }
}

.fadeUp {
  transform: translateY(25px);
  transition: all .5s ease;
  opacity: 0;
  visibility: hidden;

  &.anim-active {
    transform: none;
    opacity: 1;
    visibility: visible;
  }
}


.box-slide-left {
  position: relative;
  transform: translateX(25%);
  transition: all .8s ease;

  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 2;
    background: #FFF;
    top: 0;
    bottom: 0;
    left: 0;
    transition: all .6s ease;
    transition-delay: .2s;
  }

  &:before {
    content: '';
  }

  &.anim-active {
    transform: none;

    &:after {
      width: 0;

    }
  }
}


.box-slide-right {
  position: relative;
  transform: translateX(-25%);
  transition: all .8s ease;

  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 2;
    background: #FFF;
    top: 0;
    bottom: 0;
    right: 0;
    transition: all .6s ease;
    transition-delay: .2s;
  }

  &:before {
    content: '';
  }

  &.anim-active {
    transform: none;

    &:after {
      width: 0;

    }
  }
}


.fadeRight {
  visibility: hidden;

  span {
    opacity: 0;
    display: inline-block;
    transition: opacity .9s ease;


  }

  @for $i from 1 through 50 {
    span:nth-child(#{$i}) {
      transition-delay: .07s * $i;
    }
  }

  &.anim-active {
    visibility: visible;

    span {
      opacity: 1;

    }
  }
}


.fadeRightWord {
  visibility: hidden;

  span {
    opacity: 0;
    display: inline-block;
    transition: opacity .9s ease;
  }

  @for $i from 1 through 50 {
    span:nth-child(#{$i}) {
      transition-delay: .07s * $i;
    }
  }

  &.anim-active {
    visibility: visible;

    span {
      opacity: 1;

    }
  }
}


.opUp {
  transform: translateY(20%);
  position: relative;
  transition: opacity 1s ease, transform .7s ease;
  opacity: 0;
  visibility: hidden;

  &.anim-active {
    transform: none;
    opacity: 1;
    visibility: visible;

    &:after {
      opacity: 0;
    }

  }
}


.justFade {
  opacity: 0;
  transition: opacity .3s ease .2s;

  &.anim-active {
    opacity: 1;
  }
}


.anim-parent {
  @for $a from 1 through 30 {
    .fadeUp:nth-of-type(#{$a}) {
      transition-delay: .05s * $a
    }
  }
}

