.main_menu_wrapper{
  width: 400px;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0 , 0.8);
  backdrop-filter: blur(5px);
  height: 100%;
  display: flex;
  z-index: 9999999;
  padding: 40px 40px 100px;
  justify-content: flex-start;
  align-items: flex-end;
  transition: 1s all ease;
  transform: translateX(100%);

  &.open{
    transform: translate(0);
  }

  ul{
    li{
      margin-bottom: 20px;
      &:last-child{
        margin-bottom: 0;
      }
      a{
        font-size: 18px;
        font-weight: 500;
        line-height: 25px;
        transition: 0.7s all ease;
        &:hover{
          color: $hover;
        }
      }
    }
  }

  .close{
    position: absolute;
    height: 30PX;
    width: 30PX;
    right: 20px;
    top: 20px;
    svg{
      height: 30px;
      width: 30px;
      fill: white;
      stroke: white;
      path{
        stroke: white;
        transition: 0.7s all ease;
      }
    }

    &:hover{
      svg{
        path{
          stroke: $hover;
        }
      }
    }
  }
}

@media(max-width: 767px) {
