      right: 0;
      height: 1px;
      z-index: 999999;
      width: calc(100% - 30px);
      bottom: 0;
    }

    .row {
      .header_logo {
        z-index: 999999;
        width: 50%;

        .logo{
          margin-left: 15px;
        }
        img {
          height: 30px;
        }
      }

      .header_info {
        display: none;
        .location{
          display: none;
        }
      }


      .header_menu {
        width: 50%;

        .menu {
          max-width: unset;
        }
      }
    }


  }

  .main_menu_wrapper{
